@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

.App-header {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 34vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.question-correct {
  border: 3px solid rgb(6, 240, 15);
}

.question-incorrect {
  border: 3px solid red;
}

body {
  background-image: url(/trivia/static/media/trivia-background.54e530d2.png);
  background-size: cover;
  color: white;
  font-family: 'Pangolin', cursive;
  font-size: 23px;
}

input {
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  margin: 5px;
  width: 250px;
}

button {
  padding: 6px 30px;
  border-radius: 5px;
  font-size: 18px;
  border: none;
  margin: 10px;
  font-family: 'Pangolin', cursive;
  cursor: pointer;
  background-color: white;
}

button:disabled {
  opacity: 1;
}

#correct-answer:disabled {
  background-color: green;
  transition: 0.3s;
  opacity: 0.9;
  cursor: default;
}

.question-incorrect:disabled {
  background-color: red;
  opacity: 0.9;
  transition: 0.3s;
  cursor: default;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#input-player-name {
  padding: 8px;
  margin: 10px;
}

#input-gravatar-email {
  padding: 8px;
  margin: 10px;
  margin-bottom: 30px;
}

header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.icon-header {
  border-radius: 10px;
}

.timer {
  font-size: 26px;
}

.score {
  font-size: 26px;
}
